		handle() {},
		getDifficulty(num, attrs, index) {
			// 拿到小宝箱后要控制开启箱子的数量，因此有难度, 最后4个遗物可以控制是否打开
			const dif = attrs.relics - index;
			return dif > 3 && num === 3 ? 1 : dif;
		}
