export function generateArray(max, min = 0) {
	var arr = [];
	for (var i = min; i <= max; i++) {
		arr.push(i);
	}
	return arr;
}
export function generateRandomNumber(max, min = 0) {
	return Math.floor(Math.random() * (max - min + 1)) + min;
}
export function isVoid(val) {
	return [null, undefined].includes(val);
}
export function swapElements(arr, i, j) {
	if (i < 0 || i >= arr.length || j < 0 || j >= arr.length) {
		return arr; // 如果索引超出数组范围，则直接返回原数组
	}

	// 使用解构赋值交换两个元素的位置
	[arr[i], arr[j]] = [arr[j], arr[i]];

	return arr;
}
export function generateDigits(digit) {
	let result = "";
	for (let i = 0; i < digit; i++) {
		result += Math.floor(Math.random() * 10);
	}
	return result;
}
