<template>
  <el-card
    :class="[
      'raffle-main',
      {
        'is-loading': isLoading,
      },
    ]"
  >
    <template #footer>
      <el-space wrap>
        <el-popover effect="light" trigger="hover" placement="top" width="auto">
          <template #default>
            <div>点击后需要等待数秒到几分钟</div>
          </template>
          <template #reference>
            <el-button @click="buildRandom">随机生成</el-button>
          </template>
        </el-popover>
        <!-- <el-input-number
          min="1"
          max="10"
          v-model="randomDigits"
          controls-position="right"
          @change="handleChange"
        >
        </el-input-number> -->
      </el-space>
    </template>
    <el-form
      :model="formData"
      label-width="auto"
      label-position="top"
      class="raffle-form"
    >
      <el-form-item label="数字组合" class="raffle-form-item nums-item">
        <el-table :data="[formData.nums]" @row-click="focusNumsInput">
          <el-table-column v-for="i in 23" :key="i">
            <el-input
              ref="numsRef"
              :model-value="formData.nums[i - 1]"
              :min="1"
              :max="10"
              @click="(e) => checkIsCurrentNum(e, i - 1)"
              @input="(val) => changeNums(i - 1, val)"
            />
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item label="遗物列表" class="raffle-form-item">
        <div v-if="isLoading" class="process-container">
          <el-progress
            :percentage="loadingProcess"
            :stroke-width="15"
            striped
            striped-flow
          />
        </div>
        <el-row class="table-main">
          <el-image
            v-if="isLoading"
            fit="fill"
            :src="daidaiImg"
            class="daidai-img"
          />
          <el-col v-else>
            <el-table :data="calcRelics" height="480" @sort-change="sortByDif">
              <el-table-column min-width="180" label="达成难度" fixed sortable>
                <template #default="scope">
                  <span>{{ getRelicDifByScope(scope) }}</span>
                </template>
              </el-table-column>
              <el-table-column
                v-for="i in calcNums.length"
                :key="i"
                :label="String(i)"
                align="center"
                header-align="center"
                min-width="180"
              >
                <template #default="scope">
                  <el-button
                    circle
                    :icon="ArrowLeft"
                    @click="() => transLeft(scope)"
                  ></el-button>
                  <el-popover
                    v-if="checkIsValidByScope(scope)"
                    effect="light"
                    trigger="hover"
                    placement="top"
                    width="auto"
                  >
                    <template #default>
                      <div>遗物: {{ getRelicNameByScope(scope) }}</div>
                      <div>效果: {{ getRelicDesByScope(scope) }}</div>
                    </template>
                    <template #reference>
                      <el-tag type="primary">{{
                        getRelicNameByScope(scope)
                      }}</el-tag>
                    </template>
                  </el-popover>
                  <el-button
                    circle
                    :icon="ArrowRight"
                    @click="() => transRight(scope)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              small="small"
              layout="total, sizes, prev, pager, next, jumper"
              v-model:current-page="pager.pageIndex"
              v-model:page-size="pager.pageSize"
              :page-sizes="[10, 50, 100, 500]"
              :total="pager.total"
            />
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import { computed, ref, watch } from 'vue'
import { ArrowRight, ArrowLeft } from '@element-plus/icons-vue'
import { relicsData } from '@/views/raffle/components/relic/constance.js'
import {
  getDifficulty,
  searchRelics,
  checkNumsByRelics,
} from '@/views/raffle/js/relics.js'
import {
  isVoid,
  swapElements,
  generateRandomNumber,
} from '@/assets/script/until.js'
import get from 'lodash/get'
import daidaiImg from '../../assets/img/daidai.png'

export default {
  name: 'raffle-main',
  components: {},
  setup() {
    const randomDigits = ref(1)
    const formData = ref({
      nums: [],
      relics: [],
      difficultyList: [],
    })

    const pager = ref({
      pageIndex: 0,
      pageSize: 10,
      total: computed(() => formData.value.relics.length),
    })
    const numsRef = ref([])
    const calcNums = computed(() => formData.value.nums.join(''))
    const calcRelics = computed(() => {
      let startIndex = pager.value.pageSize * (pager.value.pageIndex - 1)
      startIndex = startIndex > 0 ? startIndex : 0
      return formData.value.relics.slice(
        startIndex,
        startIndex + pager.value.pageSize
      )
    })
    const calcDifficulty = computed(() => {
      let startIndex = pager.value.pageSize * (pager.value.pageIndex - 1)
      startIndex = startIndex > 0 ? startIndex : 0
      return formData.value.difficultyList.slice(
        startIndex,
        startIndex + pager.value.pageSize
      )
    })

    const isLoading = ref(false)
    const loadingProcess = ref(0)
    let searchTime = 0

    let relicsResultMap = {}
    watch(
      () => formData.value.nums,
      async () => {
        isLoading.value = true
        loadingProcess.value = 0
        searchTime = new Date().getTime()
        let tempTime = searchTime
        let result = [] // 监听 signal 的 abort 事件
        await searchRelics(calcNums.value, {
          relicsResultMap,
          result,
          processCallback(p) {
            if (searchTime !== tempTime) {
              return false
            }
            loadingProcess.value = parseInt(p * 100)
            return true
          },
        })
        if (searchTime !== tempTime) {
          return
        }
        const orderMap = {}
        result.forEach((li) => {
          orderMap[li.map((ele) => ele.id).join('_')] = getDifficulty(
            li,
            calcNums.value
          )
        })
        result.sort((a, b) => {
          return (
            orderMap[a.map((ele) => ele.id).join('_')] -
            orderMap[b.map((ele) => ele.id).join('_')]
          )
        })
        formData.value.difficultyList = result.map((li) => {
          return orderMap[li.map((ele) => ele.id).join('_')]
        })
        formData.value.relics = [...result]
        relicsResultMap[calcNums.value] = [...result]
        loadingProcess.value = 100
        isLoading.value = false
      },
      {
        deep: true,
      }
    )

    const methods = {
      addRelic() {},
      changeNums(i, val) {
        formData.value.nums[i] = `${val}`.slice(-1)
        const next = i + 1 === relicsData.length ? i : i + 1
        numsRef.value?.[next]?.focus()
      },
      focusNumsInput(...args) {
        let index = formData.value.nums.findIndex((ele) => !ele)
        index = index === -1 ? formData.value.nums.length : index
        numsRef.value?.[index]?.focus()
      },
      checkIsCurrentNum(e, i) {
        let index = formData.value.nums.findIndex((ele) => !ele)
        if (!isVoid(formData.value.nums[i])) {
          e.stopPropagation()
          return
        }
        numsRef.value?.[index]?.focus()
      },
      checkIsValidByScope(scope) {
        return get(scope, `row.${scope.cellIndex - 1}`, '')
      },
      getRelicIdByScope(scope) {
        return get(scope, `row.${scope.cellIndex - 1}.id`, '')
      },
      getRelicDifByScope(scope) {
        return get(calcDifficulty.value, scope.$index, '')
      },
      getRelicNameByScope(scope) {
        return get(scope, `row.${scope.cellIndex - 1}.name`, '')
      },
      getRelicDesByScope(scope) {
        return get(scope, `row.${scope.cellIndex - 1}.description`, '')
      },
      changeRelicByScope() {},
      sortByDif(order) {
        if (!order.order) {
          return
        }
        formData.value.relics.reverse()
        formData.value.difficultyList.reverse()
      },
      transLeft(scope) {
        let startIndex = pager.value.pageSize * (pager.value.pageIndex - 1)
        startIndex = startIndex > 0 ? startIndex : 0
        const leftIndex = scope.cellIndex - 2
        const index = scope.cellIndex - 1
        const arr = formData.value.relics[scope.$index + startIndex]
        swapElements(arr, index, leftIndex)
        formData.value.difficultyList[scope.$index + startIndex] =
          getDifficulty(arr, calcNums.value)
      },
      transRight(scope) {
        let startIndex = pager.value.pageSize * (pager.value.pageIndex - 1)
        startIndex = startIndex > 0 ? startIndex : 0
        const rightIndex = scope.cellIndex
        const index = scope.cellIndex - 1
        const arr = formData.value.relics[scope.$index + startIndex]
        swapElements(arr, index, rightIndex)
        formData.value.difficultyList[scope.$index + startIndex] =
          getDifficulty(arr, calcNums.value)
      },
      getValidRandomNums() {
        let numbers = Array.from({ length: 23 }, (_, index) => index) // 创建包含 0 到 23 的数组

        let randomIndex = Math.floor(Math.random() * numbers.length) // 生成随机索引
        const num = generateRandomNumber(
          relicsData[numbers[randomIndex]].max,
          relicsData[numbers[randomIndex]].min
        )
        if (checkNumsByRelics(`${formData.value.nums.join('')}${num}`)) {
          return num
        }
        return methods.getValidRandomNums()
      },
      buildRandom() {
        isLoading.value = true
        loadingProcess.value = 0
        setTimeout(() => {
          let num = methods.getValidRandomNums()
          formData.value.nums.push(num)
        }, 1000)
      },
    }
    return {
      ArrowRight,
      ArrowLeft,
      daidaiImg,
      randomDigits,
      formData,
      pager,
      numsRef,
      calcNums,
      isLoading,
      loadingProcess,
      calcRelics,
      calcDifficulty,
      relicsData,
      ...methods,
    }
  },
}
</script>
<style lang="scss" scoped>
.raffle-main {
  max-height: 100%;
  .relics-row {
    width: 100%;
  }
  .raffle-form {
    height: 100%;
  }
  .raffle-form-item {
    height: 100%;
    :deep(.el-form-item__content) {
      border-top: 1px solid var(--el-border-color);
    }
    &.nums-item {
      padding-bottom: 20px;
      :deep(.el-table__header-wrapper) {
        display: none;
      }
    }
  }
}
.table-main {
  width: 100%;
}
@keyframes spin {
  0% {
    transform: rotateY(-20deg) scaleY(1.2);
  }
  50% {
    transform: rotateY(0deg) scaleX(1.2);
  }
  100% {
    transform: rotateY(-20deg) scaleY(1.2);
  }
}
.daidai-img {
  opacity: 0.8;
  margin-top: 20px;
  width: 400px;
  animation: spin 0.3s infinite linear;
  margin: auto;
}
.process-container {
  width: 100%;
}
</style>