import orderBy from "lodash/orderBy";
import {
	initAttrs,
	relicsData,
	attrsKeys,
	setRandomAttrs,
	ROUND_ATTACK_CARD_IDS,
	ROUND_SKILL_CARD_IDS
} from "@/views/raffle/components/relic/constance.js";
const MAX_DIGITS = 23;
const initRelics = (list, attrs) => {
	attrs.relics = list.length;
	// 是否有天鹅绒颈圈
	attrs.hasCollar = Boolean(list.find((ele) => ele.id === 22));
	attrsKeys.forEach((key) => {
		setRandomAttrs[key](attrs);
	});
};
export const getDifficulty = (relicsList, num, attrs = {...initAttrs}) => {
	let difficulty = 1;
	attrs.relics = relicsList.length;
	// 是否有天鹅绒颈圈
	attrs.hasCollar = Boolean(relicsList.find((ele) => ele.id === 22));
	relicsList.forEach((ele, i) => {
		difficulty *= ele.getDifficulty(num[i], attrs, i);
	});
	return difficulty;
};
const hasDuplicateIds = (list) => {
	const idSet = new Set();
	for (const obj of list) {
		if (idSet.has(obj.id)) {
			return true; // 发现重复id，返回true
		}
		idSet.add(obj.id);
	}
	return false; // 没有发现重复id，返回false
};
const hasDuplicateGroup = (list, orderKeyMap) => {
	const orderKey = orderBy(list, ["id"])
		.map((ele) => ele.id)
		.join("_");
	if (orderKeyMap[orderKey]) {
		return true;
	}
	orderKeyMap[orderKey] = true;
	return false; // 没有发现重复id，返回false
};

const getCombinations = (arr, index, current, result, checkCallback) => {
	const list = current.slice();
	if (!checkCallback(list)) {
		return false;
	}
	if (index === arr.length) {
		result.push(list);
		return true;
	}
	for (let i = 0; i < arr[index].length; i++) {
		current.push(arr[index][i]);
		getCombinations(arr, index + 1, current, result, checkCallback);
		current.pop();
	}
	return false;
};

const checkCombinations = (list, attrs, numStr) => {
	list.forEach((ele, i) => ele.set(numStr[i], attrs));
	// 检查每个遗物的数字是否正确
	if (list.some((ele, i) => ele.get(attrs) != numStr[i])) {
		return false;
	}
	// 检查出牌数,有天鹅绒项圈最多打6张
	const maxCard = attrs.hasCollar ? 6 : attrs.round_card;
	const attackRelic = list.find((ele) => ROUND_ATTACK_CARD_IDS.includes(ele.id));
	const skillRelic = list.find((ele) => ROUND_SKILL_CARD_IDS.includes(ele.id));
	if ((attackRelic || skillRelic) && (attackRelic?.get(attrs) || 0) + (skillRelic?.get(attrs) || 0) > maxCard) {
		return false;
	}
	return true;
};

const buildRelicsList = (numStr, relicsList) => {
	const preList = [];
	// 生成digits个遗物
	for (let i = 0; i < numStr.length; i++) {
		const relics = (relicsList?.[i] ? [relicsList[i]] : relicsData).filter(
			(ele) => ele.min <= numStr[i] && ele.max >= numStr[i]
		);
		preList.push(relics);
	}
	return preList;
};

const getResultByRelics = (relicsList, numStr, current = [], result = [], buffer = {}) => {
	getCombinations(relicsList, 0, current.slice(), result, (list) => {
		if (hasDuplicateIds(list)) {
			return false;
		}
		if (hasDuplicateGroup(list, buffer)) {
			return false;
		}
		const attrs = {...initAttrs};
		initRelics(list, attrs);
		return checkCombinations(list, attrs, numStr.slice(0, list.length));
	});
	return result;
};
const runingBuildResult = (index, numStr, {preList, relicsResult, result, buffer, processCallback}) => {
	if (!relicsResult[index]?.length) {
		return {result};
	}
	getResultByRelics(preList, numStr, relicsResult[index], result, buffer);
	if(processCallback && !processCallback(index / relicsResult.length)){
		return;
	};
	return new Promise((resolve) => {
		setTimeout(() => {
			resolve(runingBuildResult(index + 1, numStr, {preList, relicsResult, result, buffer, processCallback}));
		});
	});
};
// 给定一串数字和已选定的遗物，获取其最多5位的预期遗物组合
export const searchRelics = async (numStr, params) => {
	let {relicsResultMap, result, buffer, processCallback} = params;
	relicsResultMap = relicsResultMap || {};
	result = result || [];
	buffer = buffer || {};
	// 预处理
	numStr = `${numStr || 0}`.substring(0, 23);
	for (let i = numStr.length; i > 0; i--) {
		const str1 = numStr.substring(0, i);
		if (relicsResultMap[str1]) {
			const str2 = numStr.substring(i);
			const preList = buildRelicsList(str2);
			return runingBuildResult(0, numStr, {
				preList,
				relicsResult: relicsResultMap[str1],
				result,
				buffer,
				processCallback
			});
		}
	}
	const preList = buildRelicsList(numStr);
	getResultByRelics(preList, numStr, [], result, buffer);
	return {
		result
	};
};
const isValidCombinations = (arr, index, current, result, checkCallback) => {
	const list = current.slice();
	if (!checkCallback(list)) {
		return false;
	}
	if (index === arr.length) {
		result.push(list);
		return true;
	}
	for (let i = 0; i < arr[index].length; i++) {
		current.push(arr[index][i]);
		if (isValidCombinations(arr, index + 1, current, result, checkCallback)) {
			return true;
		}
		current.pop();
	}
	return false;
};
const checkResultByRelics = (relicsList, numStr) => {
	const buffer = {};
	return isValidCombinations(relicsList, 0, [], [], (list) => {
		if (hasDuplicateIds(list)) {
			return false;
		}
		if (hasDuplicateGroup(list, buffer)) {
			return false;
		}
		const attrs = {...initAttrs};
		initRelics(list, attrs);
		if (checkCombinations(list, attrs, numStr.slice(0, list.length))) {
			return true;
		}
		return false;
	});
};

// 给定一串数字和已选定的遗物，检查该数字是否有效
export const checkNumsByRelics = (numStr, relicsList, preDigits = MAX_DIGITS) => {
	// 预处理
	numStr = `${numStr || 0}`.substring(0, 23);
	preDigits = preDigits > MAX_DIGITS ? MAX_DIGITS : preDigits;
	let digits = (relicsList?.length || 0) + preDigits;
	digits = digits > numStr.length ? numStr.length : digits;
	const preList = buildRelicsList(numStr, relicsList, digits);

	return checkResultByRelics(preList, numStr);
};

export const generateCombinations = (n, k, params) => {
	const {prefix, result, checkCallback} = params;
	if (!checkCallback(prefix)) {
		return false;
	}
	if (prefix.length === k) {
		result.push(prefix);
	} else {
		const start = prefix.length === 0 ? 0 : prefix[prefix.length - 1];
		for (let i = start; i < n; i++) {
			generateCombinations(n, k, {...params, prefix: prefix.concat(i)});
		}
	}
	return result;
};

export const traverseCombinations = async (digits, relicsList) => {
	digits = parseInt(digits);
	let result = [];
	const list = digits === 1 ? [] : relicsList;
	const total = list.length ? list.length : 1;
	for (let i = 0; i < total; i++) {
		generateCombinations(10, digits, {
			prefix: list[i] || [],
			result,
			checkCallback(prefix) {
				return prefix.length ? checkNumsByRelics(prefix.join("")) : true;
			}
		});
	}
	return result;
};
